<template>
  <p class="desc" v-if="isShow">
    <span class="key">{{text}}:</span>
    {{ value }}
  </p>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      default: 'xxxx'
    },
    value: {
      type: [String, Number, Boolean]
    }
  },
  computed: {
    isShow () {
      return !(this.value == null)
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/theme'

  .desc
    margin: 10px
    font-size: $font-size-large-xxl
    .key
      color: $text-color
      font-size: $font-size-large-xl
      margin-right: 5px
  @media screen and (max-width: 480px)
    .desc
      margin: 6px
      font-size: $font-size-large-xl
      .key
        font-size: $font-size-large
</style>



