<template>
  <div class="form-wrapper">
    <form @submit.prevent="submitHandler">
      <input-field
        label="请输入您的名字"
        icon="icon-username"
        v-model="username"
        :autofocus="true"
        class="username"
        errorText="名字不能为空"
        :showError="nameError"
      />
      <input-field
        label="请输入您的手机号码"
        icon="icon-phone"
        v-model="telphone"
        class="phone"
        type="phone"
        errorText="手机号码不能为空"
        :showError="telError"
      />
      <input type="submit" value="匹配"/>
    </form>
  </div>
</template>

<script>
import InputField from 'base/inputField/inputField'
import { searchFriend } from 'api/search'

export default {
  data () {
    return {
      username: '',
      telphone: '',
      nameError: false,
      telError: false,
      result: null
    }
  },
  methods: {
    // 提交表单
    submitHandler () {
      const { username, telphone } = this
      if (username === '' || telphone === '') {
        if (username === '') {
          this.nameError = true
        }
        if (telphone === '') {
          this.telError = true
        }
        return undefined
      }
      searchFriend({
        username,
        telphone
      }).then(res => {
        if (res.status === 200) {
          // 返回内容为空
          if (res.data == null) {
            this.$router.push({
              path: '/empty'
            })
          } else {
            this.$eventBus.$emit('success', res.data)
            this.$router.push({
              path: '/result'
            })
          }
        }
      }).catch(err => {
        if (err) {
          alert('网络出错!')
        }
      })
    }
  },
  components: {
    InputField
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/theme'

  .form-wrapper
    width: 100%
    height: 100%
    display: flex
    align-items: center
    justify-content: center
    form
      width: 500px
      text-align: center
      input[type=submit]
        width: 300px
        text-align: center
        line-height: 50px
        background-color: $text-color
        color: #fff
    @media screen and (max-width: 480px)
      form
        width: 240px
        input[type=submit]
          width: 230px
</style>



