import Vue from 'vue'
import Router from 'vue-router'

import Search from 'components/search/search'
import Empty from 'components/empty/empty'
import Result from 'components/Result/Result'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Search
    },
    {
      path: '/empty',
      component: Empty
    },
    {
      path: '/result',
      component: Result
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



