<template>
  <div class="input-field">
    <div class="input-icon">
      <i class="iconfont" :class="icon"></i>
    </div>
    <label class="input-label">{{ label }}</label>
    <input
      type="text"
      :value="value"
      @input="inputHandler($event)"
    >
    <div
      v-show="err"
      class="error"
    >
    {{ errorText }}
    </div>
  </div>
</template>

<script>
export default {
  props: {
    icon: {
      type: String,
      default: 'icon-username'
    },
    label: {
      type: String,
      default: '请输入....'
    },
    autofocus: {
      type: Boolean,
      default: false
    },
    value: {
      type: [String, Number],
      default: ''
    },
    type: {
      type: String,
      default: 'text'
    },
    errorText: {
      type: String,
      default: '不能为空'
    },
    showError: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      error: false
    }
  },
  computed: {
    err () {
      if (this.value !== '' && this.showError === true) {
        return false
      }
      return this.error || this.showError
    }
  },
  watch: {
    value (newVal) {
      if (newVal === '') {
        this.error = true
      } else {
        this.error = false
      }
    }
  },
  methods: {
    inputHandler (ev) {
      let target = ev.target
      if (this.type === 'phone') {
        target.value = target.value.replace(/[^\d]/g, '')
      }
      this.$emit('input', target.value.trim())
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/theme'

  .input-field
    color: $text-color
    text-align: center
    margin-bottom: 20px
    .input-icon
      margin-bottom: 20px
      .iconfont
        font-size: $font-size-large-xxl
    .input-label
      display: block
      font-size: $font-size-large-xl
    input
      width: 300px
      padding: 5px 10px
      font-size: $font-size-large-xxl
      outline: none
      text-align: center
      border: none
      border-bottom: 1px solid $text-color
      letter-spacing: 2px
    .error
      margin-top: 10px
      color: $error-color
      font-size: $font-size-small
    @media screen and (max-width: 480px)
      input
        width: 240px
        padding: 5px 1px
</style>




