// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import 'common/stylus/index.styl'

Vue.config.productionTip = false

const bus = new Vue({
  data () {
    return {
      result: null
    }
  },
  created () {
    this.$on('success', this.successHandler)
  },
  beforeDestroy () {
    this.$off('success', this.successHandler)
  },
  methods: {
    successHandler (val) {
      this.result = val
    }
  }
})

Object.defineProperties(Vue.prototype, {
  $eventBus: {
    get: () => {
      return bus
    }
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



